import GlideSignals from 'sn-signals';
import { SEISMIC_OR_MOBILE_APPLICATION } from './constants';

const signalTracker = {
    trackEvent(eventAction, eventPriority, eventModel) {
        eventModel.applicationType = SEISMIC_OR_MOBILE_APPLICATION;
        GlideSignals.trackEvent(eventAction, eventPriority, eventModel);
    },
};

export default signalTracker;
