import { INITIAL_DRILL_HEADER } from './constants';

export const navigatedToParentPanelEffect = ({ updateState }) => {
    updateState({
        drillHeader: INITIAL_DRILL_HEADER,
    });
};

export const navigatedToChildPanelEffect = ({ action, updateState }) => {
    const {
        payload: { itemValue },
    } = action;

    updateState({
        drillHeader: itemValue,
    });
};
