import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import rtlBehavior from '@servicenow/behavior-rtl';
import view from './view/MobileView';
import styles from './scss/index.scss';
import actionHandlers from './actionHandlers';
import { DEFAULT_LABEL } from '../common/constants';
import { INITIAL_DRILL_HEADER } from './constants';

createCustomElement('sn-search-facets-mobile', {
    renderer: { type: snabbdom },
    view,
    initialState: {
        facetsMetadata: {},
        facetsWithOptions: [],
        drillHeader: INITIAL_DRILL_HEADER,
        truncateAppliedFacets: true,
    },
    properties: {
        facets: {
            default: [],
            schema: {
                type: 'array',
            },
        },
        label: {
            default: DEFAULT_LABEL,
            schema: {
                type: 'string',
            },
        },
        selectedOptions: {
            default: [],
            schema: {
                type: 'array',
            },
        },
    },
    behaviors: [{ behavior: rtlBehavior }],
    styles,
    actionHandlers,
});
