import cloneDeep from 'lodash/cloneDeep';

// eslint-disable-next-line import/prefer-default-export
export const sectionFoldToggledEffect = ({ action, state, updateState }) => {
    const { facetsMetadata } = state;
    const updatedFacetsMetadata = cloneDeep(facetsMetadata);
    const {
        payload: { value },
    } = action;

    updatedFacetsMetadata[value].expanded = !updatedFacetsMetadata[value]
        .expanded;
    updateState({ facetsMetadata: updatedFacetsMetadata });
};
