import '@servicenow/now-heading';
import ExpandButton from './ExpandButton';
import { NAVIGATED_TO_CHILD_PANEL } from '../actions';

const Filters = ({ dispatch, label, facetsMetadata, expanded }) => (
    <div
        className="drill-filters"
        tabIndex={0}
        role="button"
        on-click={() =>
            dispatch(NAVIGATED_TO_CHILD_PANEL, {
                itemValue: label,
            })
        }
    >
        <div className="drill-filter">
            <div className="drill-filter-text">
                <now-heading
                    label={label}
                    level="2"
                    variant="title-primary"
                    hasNoMargin
                />
            </div>
        </div>
        <div className="drill-filters-controls">
            <ExpandButton
                dispatch={dispatch}
                category={label}
                expanded={expanded}
                facetsMetadata={facetsMetadata}
            />
        </div>
    </div>
);

export default Filters;
