import '@servicenow/now-collapse';

import Facet from '../../common/view/Facet';
import Divider from '../../common/view/Divider';
import Accordion from './Accordion';
import { MODE } from '../../common/constants';

const Panel = ({
    dispatch,
    label,
    expanded,
    facetsMetadata,
    facet,
    showEndingDivider,
    nowId,
}) => (
    <div role="treeitem">
        <Divider variant="tertiary" />
        <Accordion
            dispatch={dispatch}
            label={label}
            expanded={expanded}
            facetsMetadata={facetsMetadata}
            nowId={nowId}
        />
        <now-collapse expanded={expanded}>
            <Facet
                facet={facet}
                facetsMetadata={facetsMetadata}
                mode={MODE.TREE}
                nowId={nowId}
            />
        </now-collapse>
        {showEndingDivider ? <Divider variant="tertiary" /> : null}
    </div>
);

export default Panel;
