import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';

import view from './view';
import {
    SIZE,
    DEFAULT_LABEL,
    SEARCH_REFINED_EVENT,
    INFO_EVENT,
} from './common/constants';
import {
    FACET_SELECTIONS_UPDATED_INTERNAL,
    FACET_SELECTIONS_UPDATED,
    DONE_BUTTON_CLICKED,
} from './common/actions';
import { DONE_BUTTON_CLICKED_INTERNAL } from './sn-search-facets-mobile/actions';

import signalTracker from './common/signalTracker';

const facetSelectionsUpdatedEffect = ({
    action: { payload },
    dispatch,
    properties,
}) => {
    const { searchContextConfigId, searchTerm } = properties;
    signalTracker.trackEvent(SEARCH_REFINED_EVENT, INFO_EVENT, {
        searchContextConfigId,
        queryTerm: searchTerm,
    });

    dispatch(FACET_SELECTIONS_UPDATED, payload);
};

const mobileDoneButtonClickedEffect = ({ dispatch }) => {
    dispatch(DONE_BUTTON_CLICKED);
};

/**
 * Seismic component for search facets that can be used to refine search results.
 * Allows users to narrow a large set of search results down to only those that match the specific criteria.
 *
 * ```jsx
 * <sn-search-facets
 *      size="lg"
 *      facets=[
 *          {
 *              type: 'multi-select',
 *              name: 'Location',
 *              optionLimit: 10,
 *              data: {
 *                  count: 11,
 *                  options: [
 *                      {
 *                          label: 'North America',
 *                          value: 'North America',
 *                          count: 5,
 *                          options: null,
 *                      },
 *                      {
 *                          label: 'Asia Pacific',
 *                          value: 'Asia Pacific',
 *                          count: 4,
 *                          options: null,
 *                      },
 *                      {
 *                          label: 'Latin America',
 *                          value: 'Latin America',
 *                          count: 2,
 *                          options: null,
 *                      },
 *                  ],
 *              },
 *          },
 *          {
 *              type: 'multi-select',
 *              name: 'Category',
 *              optionLimit: 10,
 *              data: {
 *                  options: [
 *                      {
 *                          label: 'Email',
 *                          value: 'Email',
 *                          options: null,
 *                      },
 *                      {
 *                          label: 'Hardware',
 *                          value: 'Hardware',
 *                          options: null,
 *                      },
 *                      {
 *                          label: 'Change',
 *                          value: 'Change',
 *                          options: null,
 *                      }
 *                  ],
 *              },
 *          },
 *      ]
 *      label="Filters"
 * />
 * ```
 *
 * @seismicElement sn-search-facets
 * @summary Seismic component for search facets that can be used to refine search results.
 */
createCustomElement('sn-search-facets', {
    renderer: { type: snabbdom },
    view,
    properties: {
        /**
         * Contains the search facets data from search service.
         * @type {array}
         */
        facets: {
            default: [],
            schema: {
                type: 'array',
            },
        },
        /**
         * This property specifies the label that's shown on top of the facet panel.
         * @type {string}
         */
        label: {
            default: DEFAULT_LABEL,
            schema: {
                type: 'string',
            },
        },
        /**
         * Specifies if the component is being rendered on mobile or desktop.
         * There are two accepted values for this property, 'sm' for mobile version and 'lg' for desktop version.
         * @type {'sm'|'lg'}
         */
        size: {
            default: SIZE.LARGE,
            schema: {
                type: 'string',
                enum: ['sm', 'lg'],
            },
        },
        /**
         * Specifies the options that should be selected on initial render.
         * @type {array}
         */
        selectedOptions: {
            default: [],
            schema: {
                type: 'array',
            },
        },
        /**
         * sys_id of the search context configuration.
         * Given searchContextConfigId, our search service can retrieve all the search and autocomplete configuration metadata.
         * This sys_id is defined in sys_search_context_config table.
         * @type {string}
         */
        searchContextConfigId: {
            default: '',
            schema: {
                type: 'string',
            },
        },
        /**
         * The search term for which the genius results and search results are shown.
         * @type {string}
         */
        searchTerm: {
            default: '',
            schema: {
                type: 'string',
            },
        },
    },
    actionHandlers: {
        [FACET_SELECTIONS_UPDATED_INTERNAL]: {
            effect: facetSelectionsUpdatedEffect,
            stopPropagation: true,
        },
        [DONE_BUTTON_CLICKED_INTERNAL]: {
            effect: mobileDoneButtonClickedEffect,
            stopPropagation: true,
        },
    },
    dispatches: {
        /**
         * Dispatched when a user selects any of the options.
         * The payload contains all the selected filter values, which can then be sent with the search query request to get relevant search results.
         * @type {{selectedFilters: string}}
         */
        'SN_SEARCH_FACETS#SELECTIONS_UPDATED': {
            schema: {
                type: 'object',
                properties: {
                    selectedFilters: { type: 'string' },
                },
            },
        },
    },
});
