import '@servicenow/now-button';
import '@servicenow/now-heading';
import { DONE_BUTTON_CLICKED_INTERNAL } from '../actions';
import { ALL_CLEARED } from '../../common/actions';
import { CLEAR_ALL_LABEL } from '../../common/constants';
import { DONE_LABEL } from '../constants';
import { _anyItemsSelected } from '../../common/utils';

const RootHeader = ({
    label,
    dispatch,
    appliedFacetFilters,
    facetsMetadata,
    nowId,
}) => {
    const className = _anyItemsSelected(facetsMetadata, appliedFacetFilters)
        ? 'clear-button'
        : 'clear-button-hidden';
    return (
        <div className="drill-root-header">
            <now-heading
                variant="title-primary"
                label={label}
                level="1"
                hasNoMargin
                id={`${nowId}_tree_label`}
            />
            <div className="drill-root-header-right">
                <div className={className}>
                    <now-button-bare
                        label={CLEAR_ALL_LABEL}
                        size="sm"
                        hidePadding
                        on-click={() => {
                            dispatch(ALL_CLEARED, { facetsMetadata });
                        }}
                    />
                </div>
                <now-button-bare
                    label={DONE_LABEL}
                    size="sm"
                    hidePadding
                    on-click={() => {
                        dispatch(DONE_BUTTON_CLICKED_INTERNAL);
                    }}
                />
            </div>
        </div>
    );
};

export default RootHeader;
