import '@servicenow/now-pill';
import '@servicenow/now-button';
import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import { t } from 'sn-translate';
import Divider from './Divider';
import {
    APPLIED_FACETS_TRUNCATION,
    APPLIED_FACET_LIST,
    SHOW_FEWER_LABEL,
} from '../constants';
import { _getAppliedFacetFilterLabels } from '../utils';

const AppliedFacets = ({
    appliedFacetFilters,
    truncateAppliedFacets,
    showEndingDivider,
}) => {
    const shouldTruncate =
        appliedFacetFilters.length > APPLIED_FACETS_TRUNCATION;
    const showMoreLabel = t(
        '+ {0} more',
        appliedFacetFilters.length - APPLIED_FACETS_TRUNCATION
    );
    const appliedFilterLabels = _getAppliedFacetFilterLabels(
        appliedFacetFilters,
        truncateAppliedFacets
    );

    return appliedFacetFilters.length > 0 ? (
        <Fragment>
            <Divider variant="tertiary" />
            <div className="applied-facets">
                {appliedFilterLabels.map(filter => (
                    <now-pill
                        label={filter}
                        size="sm"
                        className="applied-facet-pill"
                        tabIndex={-1}
                        selected
                        manageSelected
                    />
                ))}
                {shouldTruncate && truncateAppliedFacets ? (
                    <now-button-bare
                        label={showMoreLabel}
                        size="sm"
                        className="applied-facet-pill"
                        append-to-payload={{
                            action: APPLIED_FACET_LIST.EXPAND,
                        }}
                        hidePadding
                    />
                ) : (
                    <Fragment />
                )}
                {shouldTruncate && !truncateAppliedFacets ? (
                    <now-button-bare
                        label={SHOW_FEWER_LABEL}
                        size="sm"
                        className="applied-facet-pill"
                        append-to-payload={{
                            action: APPLIED_FACET_LIST.COLLAPSE,
                        }}
                        hidePadding
                    />
                ) : (
                    <Fragment />
                )}
            </div>
            {showEndingDivider ? <Divider variant="tertiary" /> : ''}
        </Fragment>
    ) : (
        ''
    );
};

export default AppliedFacets;
