import '@servicenow/now-icon';
import { SECTION_FOLD_TOGGLED } from '../actions';
import {
    COLLAPSE_LABEL,
    EXPAND_LABEL,
    SPACE_KEY,
    ENTER_KEY,
} from '../../common/constants';

const _getIcon = expanded => (expanded ? 'minus-fill' : 'plus-fill');

const ExpandButton = ({ dispatch, expanded, label, nowId }) => {
    const icon = _getIcon(expanded);
    const ariaTitle = expanded ? COLLAPSE_LABEL : EXPAND_LABEL;

    const clickHandler = event => {
        if (
            event.type === 'click' ||
            event.keyCode === SPACE_KEY ||
            event.keyCode === ENTER_KEY
        ) {
            dispatch(SECTION_FOLD_TOGGLED, {
                value: label,
            });
        }
    };

    return (
        <div
            className="tree-expand-button"
            role="button"
            title={ariaTitle}
            aria-label={`${ariaTitle}, ${label}`}
            tabIndex={0}
            on-click={clickHandler}
            on-keyup={clickHandler}
        >
            <div className="tree-expand-button-focus">
                <now-icon icon={icon} size="sm" />
            </div>
            <div
                className="accordion-title"
                id={`${nowId}_${label}_facet_category`}
            >
                {label}
            </div>
        </div>
    );
};

export default ExpandButton;
