import cloneDeep from 'lodash/cloneDeep';
import { _buildState } from './utils';
import { FACET_SELECTIONS_UPDATED_INTERNAL } from './actions';
import { APPLIED_FACET_LIST } from './constants';

const _allSelectedFilters = facetsMetadata =>
    JSON.stringify(
        Object.keys(facetsMetadata)
            .filter(
                category =>
                    facetsMetadata[category].selectedFacetFilters.length > 0
            )
            .reduce((currentSelectedFilters, category) => {
                const { selectedFacetFilters } = facetsMetadata[category];
                return [...currentSelectedFilters, ...selectedFacetFilters];
            }, [])
    );

export const componentBootstrappedEffect = ({
    state,
    properties,
    updateState,
}) => {
    updateState(_buildState({ state, properties }));
};

export const componentPropertyChangedEffect = ({
    action,
    state,
    updateState,
    properties,
}) => {
    const {
        payload: { name },
    } = action;

    if (name === 'facets' || name === 'selectedOptions') {
        updateState(_buildState({ state, properties }));
    }
};

export const selectionsUpdatedEffect = ({
    state,
    action,
    updateState,
    dispatch,
}) => {
    const { facetsMetadata } = state;
    const {
        payload: { category, selectedFacetFilters },
    } = action;
    const updatedFacetsMetadata = cloneDeep(facetsMetadata);
    const updatedFacet = updatedFacetsMetadata[category];
    const selectedFilterValues = selectedFacetFilters.map(
        option => option.value
    );

    updatedFacetsMetadata[category] = {
        ...updatedFacet,
        selectedFacetFilters: selectedFilterValues,
    };

    updateState({
        facetsMetadata: updatedFacetsMetadata,
    });

    dispatch(FACET_SELECTIONS_UPDATED_INTERNAL, {
        selectedFilters: _allSelectedFilters(updatedFacetsMetadata),
    });
};

export const allFacetsClearedEffect = ({ state, updateState, dispatch }) => {
    const { facetsMetadata } = state;
    const updatedFacetsMetadata = cloneDeep(facetsMetadata);

    Object.keys(updatedFacetsMetadata).forEach(category => {
        updatedFacetsMetadata[category] = {
            ...updatedFacetsMetadata[category],
            selectedFacetFilters: [],
        };
    });
    updateState({
        facetsMetadata: updatedFacetsMetadata,
    });
    dispatch(FACET_SELECTIONS_UPDATED_INTERNAL, {
        selectedFilters: _allSelectedFilters(updatedFacetsMetadata),
    });
};

export const accordionClearedEffect = ({
    action,
    state,
    updateState,
    dispatch,
}) => {
    const {
        payload: { value },
    } = action;
    const { facetsMetadata } = state;
    const updatedFacetsMetadata = cloneDeep(facetsMetadata);

    updatedFacetsMetadata[value] = {
        ...updatedFacetsMetadata[value],
        selectedFacetFilters: [],
    };
    updateState({
        facetsMetadata: updatedFacetsMetadata,
    });
    dispatch(FACET_SELECTIONS_UPDATED_INTERNAL, {
        selectedFilters: _allSelectedFilters(updatedFacetsMetadata),
    });
};

export const expandCollapseAppliedFacetsEffect = ({ action, updateState }) => {
    const { payload } = action;
    if (payload.action) {
        if (payload.action === APPLIED_FACET_LIST.EXPAND)
            updateState({ truncateAppliedFacets: false });

        if (payload.action === APPLIED_FACET_LIST.COLLAPSE)
            updateState({ truncateAppliedFacets: true });
    }
};
