import { APPLIED_FACETS_TRUNCATION } from './constants';

const _getNonEmptyFacets = facets => {
    if (!Array.isArray(facets) || !facets) {
        return [];
    }

    return facets.filter(facet => {
        const {
            data: { options },
        } = facet;
        return options.length > 0;
    });
};

const _getExpanded = (facetMetadata, index) => {
    if (index === 0) return true;
    if (facetMetadata) return facetMetadata.expanded;
    return false;
};

const _getValues = options => {
    if (Array.isArray(options))
        return options.reduce(
            (acc, option) =>
                acc.concat(option.value, _getValues(option.options)),
            []
        );
    return [];
};

const _getSelectedFilters = (options, selectedOptions) => {
    if (selectedOptions.length === 0) return [];

    const values = _getValues(options);
    return values.filter(option => selectedOptions.includes(option));
};

const _buildFacetMetadata = (facets, facetsMetadata, selectedOptions) => {
    const updatedFacetsMetadata = {};

    facets.forEach((facet, index) => {
        const {
            name,
            data: { options },
        } = facet;

        updatedFacetsMetadata[name] = {
            expanded: _getExpanded(facetsMetadata[name], index),
            selectedFacetFilters: _getSelectedFilters(options, selectedOptions),
        };
    });

    return updatedFacetsMetadata;
};

export const _buildState = ({ state, properties }) => {
    const { facetsMetadata } = state;

    const { facets, selectedOptions } = properties;

    const facetsWithOptions = _getNonEmptyFacets(facets);
    const updatedFacetsMetadata = _buildFacetMetadata(
        facetsWithOptions,
        facetsMetadata,
        selectedOptions
    );

    return {
        ...state,
        facetsMetadata: updatedFacetsMetadata,
        facetsWithOptions,
    };
};

// returns true if any of the facet items are selected
export const _anyItemsSelected = (facetsMetadata, appliedFacetFilters) => {
    const selectionsFromMetadata = Object.keys(facetsMetadata).some(
        category => {
            const { selectedFacetFilters } = facetsMetadata[category];
            return selectedFacetFilters.length > 0;
        }
    );
    const appliedFiltersReturned =
        appliedFacetFilters && appliedFacetFilters.length > 0;

    return selectionsFromMetadata || appliedFiltersReturned;
};

// returns the number of selected facet items for a category
export const _selectedItemsLength = (facetsMetadata, category) => {
    const { selectedFacetFilters } = facetsMetadata[category];
    return selectedFacetFilters.length;
};

const _getLabelsFromFilters = facetFilters => {
    if (!Array.isArray(facetFilters) || !facetFilters) {
        return [];
    }

    const regExp = /FACET\("*([^)"]+)"*\)/;
    return facetFilters.map(filter => filter.match(regExp)[1]);
};

export const _getAppliedFacetFilterLabels = (
    appliedFacetFilters,
    truncateAppliedFacets
) => {
    const appliedFilterLabels = _getLabelsFromFilters(appliedFacetFilters);
    if (
        truncateAppliedFacets &&
        appliedFilterLabels.length > APPLIED_FACETS_TRUNCATION
    )
        return appliedFilterLabels.slice(0, APPLIED_FACETS_TRUNCATION);

    return appliedFilterLabels;
};
