import PanelList from './PanelList';
import Header from './Header';

const view = (state, { dispatch }) => {
    const {
        facetsMetadata,
        facetsWithOptions,
        drillHeader,
        properties,
        truncateAppliedFacets,
    } = state;
    const { label, selectedOptions, nowId } = properties;

    return (
        <panel className="facet-panel-drill">
            <Header
                dispatch={dispatch}
                label={label}
                facetsMetadata={facetsMetadata}
                drillHeader={drillHeader}
                appliedFacetFilters={selectedOptions}
                nowId={nowId}
            />
            <PanelList
                dispatch={dispatch}
                facets={facetsWithOptions}
                facetsMetadata={facetsMetadata}
                drillHeader={drillHeader}
                appliedFacetFilters={selectedOptions}
                truncateAppliedFacets={truncateAppliedFacets}
                nowId={nowId}
            />
        </panel>
    );
};

export default view;
