import { Fragment } from '@servicenow/ui-renderer-snabbdom';

import Facet from '../../common/view/Facet';
import Divider from '../../common/view/Divider';
import Filters from './Filters';
import { INITIAL_DRILL_HEADER } from '../constants';
import { MODE } from '../../common/constants';

const Panel = ({
    drillHeader,
    facetsMetadata,
    label,
    dispatch,
    expanded,
    facet,
    showEndingDivider,
    nowId,
}) => {
    if (drillHeader === INITIAL_DRILL_HEADER) {
        return (
            <div role="treeitem">
                <Divider variant="tertiary" />
                <Filters
                    label={label}
                    dispatch={dispatch}
                    facetsMetadata={facetsMetadata}
                    expanded={expanded}
                />
                {showEndingDivider ? <Divider variant="tertiary" /> : null}
            </div>
        );
    }

    if (label === drillHeader) {
        return (
            <div className="facet-options">
                <Facet
                    facet={facet}
                    facetsMetadata={facetsMetadata}
                    mode={MODE.DRILL}
                    nowId={nowId}
                />
            </div>
        );
    }

    return <Fragment />;
};

export default Panel;
