import { actionTypes } from '@servicenow/ui-core';
import {
    SELECTIONS_UPDATED,
    ALL_CLEARED,
    ACCORDION_CLEARED,
    APPLIED_FACETS_EXPANDED_COLLAPSED,
} from './actions';
import {
    componentBootstrappedEffect,
    selectionsUpdatedEffect,
    allFacetsClearedEffect,
    accordionClearedEffect,
    componentPropertyChangedEffect,
    expandCollapseAppliedFacetsEffect,
} from './effects';

const { COMPONENT_BOOTSTRAPPED, COMPONENT_PROPERTY_CHANGED } = actionTypes;

export default {
    [COMPONENT_BOOTSTRAPPED]: componentBootstrappedEffect,
    [COMPONENT_PROPERTY_CHANGED]: componentPropertyChangedEffect,
    [SELECTIONS_UPDATED]: {
        effect: selectionsUpdatedEffect,
        stopPropagation: true,
    },
    [ALL_CLEARED]: {
        effect: allFacetsClearedEffect,
        stopPropagation: true,
    },
    [ACCORDION_CLEARED]: {
        effect: accordionClearedEffect,
        stopPropagation: true,
    },
    [APPLIED_FACETS_EXPANDED_COLLAPSED]: {
        effect: expandCollapseAppliedFacetsEffect,
        stopPropagation: true,
    },
};
