import { INITIAL_DRILL_HEADER } from '../constants';
import RootHeader from './RootHeader';
import CategoryHeader from './CategoryHeader';

const Header = ({
    facetsMetadata,
    drillHeader,
    label,
    appliedFacetFilters,
    dispatch,
    nowId,
}) => {
    if (drillHeader === INITIAL_DRILL_HEADER) {
        return (
            <RootHeader
                label={label}
                dispatch={dispatch}
                facetsMetadata={facetsMetadata}
                appliedFacetFilters={appliedFacetFilters}
                nowId={nowId}
            />
        );
    }

    return (
        <CategoryHeader
            drillHeader={drillHeader}
            dispatch={dispatch}
            label={drillHeader}
            facetsMetadata={facetsMetadata}
            nowId={nowId}
        />
    );
};

export default Header;
