import '@servicenow/now-button';
import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import { t } from 'sn-translate';

import ExpandButton from './ExpandButton';
import { ACCORDION_CLEARED } from '../../common/actions';
import { _selectedItemsLength } from '../../common/utils';

const Accordion = ({ expanded, facetsMetadata, dispatch, label, nowId }) => {
    const numSelectedItems = _selectedItemsLength(facetsMetadata, label);

    return (
        <div
            className={expanded ? 'accordion-expanded' : 'accordion'}
            aria-expanded={expanded ? 'true' : 'false'}
        >
            <div className="accordion-controls">
                <ExpandButton
                    dispatch={dispatch}
                    label={label}
                    expanded={expanded}
                    nowId={nowId}
                />
            </div>
            <div className="clear-button">
                {numSelectedItems > 0 ? (
                    <now-button-bare
                        label={t('Clear ({0})', numSelectedItems)}
                        configAria={{
                            'aria-label': t(
                                `Clear (${numSelectedItems}), ${label} filter`
                            ),
                        }}
                        size="sm"
                        on-click={() => {
                            dispatch(ACCORDION_CLEARED, {
                                value: label,
                            });
                        }}
                        hidePadding
                    />
                ) : (
                    <Fragment />
                )}
            </div>
        </div>
    );
};

export default Accordion;
