import Panel from './Panel';
import AppliedFacets from '../../common/view/AppliedFacets';

const PanelList = ({
    facets,
    facetsMetadata,
    appliedFacetFilters,
    truncateAppliedFacets,
    nowId,
    dispatch,
}) => (
    <ul className="panel" role="tree" aria-labelledby={`${nowId}_tree_label`}>
        <AppliedFacets
            appliedFacetFilters={appliedFacetFilters}
            truncateAppliedFacets={truncateAppliedFacets}
            showEndingDivider={facets.length === 0}
        />
        {facets.map((facet, index) => {
            const { name } = facet;
            const isExpanded = facetsMetadata[name].expanded;
            const showEndingDivider = index === facets.length - 1;

            return (
                <Panel
                    dispatch={dispatch}
                    label={name}
                    expanded={isExpanded}
                    facetsMetadata={facetsMetadata}
                    facet={facet}
                    showEndingDivider={showEndingDivider}
                    nowId={nowId}
                />
            );
        })}
    </ul>
);

export default PanelList;
