import '@devsnc/sn-hierarchical-select';

import { SELECTOR_TYPE } from '../constants';

const Facet = ({ facet, facetsMetadata, mode, nowId }) => {
    const { name, optionLimit } = facet;
    const selectedItems = facetsMetadata[name].selectedFacetFilters;

    return (
        <sn-hierarchical-select
            select={SELECTOR_TYPE.MULTIPLE}
            items={facet}
            optionLimit={optionLimit}
            mode={mode}
            category={name}
            selectedItems={selectedItems}
            containerId={nowId}
        />
    );
};

export default Facet;
