import '@servicenow/now-button';
import '@servicenow/now-heading';
import { ALL_CLEARED } from '../../common/actions';
import { CLEAR_ALL_LABEL, CLEAR_ALL_WCAG_LABEL } from '../../common/constants';
import { _anyItemsSelected } from '../../common/utils';

const Header = ({
    facetsMetadata,
    label,
    appliedFacetFilters,
    nowId,
    dispatch,
}) => (
    <div className="tree-root-header">
        <now-heading
            variant="title-primary"
            level={2}
            label={label}
            hasNoMargin
            id={`${nowId}_tree_label`}
        />
        <div
            className={
                _anyItemsSelected(facetsMetadata, appliedFacetFilters)
                    ? 'clear-button'
                    : 'clear-button-hidden'
            }
        >
            <now-button-bare
                label={CLEAR_ALL_LABEL}
                configAria={{ 'aria-label': CLEAR_ALL_WCAG_LABEL }}
                size="sm"
                hidePadding
                on-click={() => {
                    dispatch(ALL_CLEARED, {});
                }}
            />
        </div>
    </div>
);

export default Header;
