import '@servicenow/now-icon';
import { NAVIGATED_TO_CHILD_PANEL } from '../actions';
import { COLLAPSE_LABEL, EXPAND_LABEL } from '../../common/constants';

const ExpandButton = ({ dispatch, expanded, category }) => {
    const className = 'drill-expand-button';
    const ariaTitle = expanded ? COLLAPSE_LABEL : EXPAND_LABEL;

    return (
        <div
            className={className}
            title={ariaTitle}
            tabIndex={-1}
            on-click={() =>
                dispatch(NAVIGATED_TO_CHILD_PANEL, {
                    itemValue: category,
                })
            }
        >
            <now-icon icon="chevron-right-outline" size="md" />
        </div>
    );
};

export default ExpandButton;
