import '@servicenow/now-button';
import '@servicenow/now-heading';
import {
    NAVIGATED_TO_PARENT_PANEL,
    DONE_BUTTON_CLICKED_INTERNAL,
} from '../actions';
import { ACCORDION_CLEARED } from '../../common/actions';
import { _selectedItemsLength } from '../../common/utils';
import { BACK_LABEL, DONE_LABEL } from '../constants';
import { CLEAR_LABEL } from '../../common/constants';

const CategoryHeader = ({ dispatch, label, facetsMetadata, nowId }) => {
    const className =
        _selectedItemsLength(facetsMetadata, label) > 0
            ? 'clear-button'
            : 'clear-button-hidden';

    return (
        <div className="drill-header">
            <div className="drill-header-left" role="button">
                <now-button-bare
                    icon-start="chevron-left-outline"
                    label={BACK_LABEL}
                    size="sm"
                    variant="primary"
                    hidePadding
                    on-click={() => dispatch(NAVIGATED_TO_PARENT_PANEL, {})}
                />
            </div>
            <now-heading
                label={label}
                variant="title-primary"
                hasNoMargin
                id={`${nowId}_${label}_facet_category`}
            />
            <div className="drill-header-right">
                <div className={className}>
                    <now-button-bare
                        label={CLEAR_LABEL}
                        size="sm"
                        hidePadding
                        on-click={() => {
                            dispatch(ACCORDION_CLEARED, {
                                value: label,
                            });
                        }}
                    />
                </div>
                <div className="done-button">
                    <now-button-bare
                        label={DONE_LABEL}
                        size="sm"
                        on-click={() => dispatch(DONE_BUTTON_CLICKED_INTERNAL)}
                        hidePadding
                    />
                </div>
            </div>
        </div>
    );
};

export default CategoryHeader;
