import isEmpty from 'lodash/isEmpty';

import PanelList from './PanelList';
import Header from './Header';

const _isEmptyFacet = facets => {
    if (isEmpty(facets)) return true;

    return facets.every(facetObject => facetObject.data.options.length === 0);
};

const view = (state, { dispatch }) => {
    const {
        facetsMetadata,
        facetsWithOptions,
        truncateAppliedFacets,
        properties,
    } = state;
    const { label, facets, selectedOptions, nowId } = properties;
    const showFacets = !_isEmptyFacet(facets) || !isEmpty(selectedOptions);

    return showFacets ? (
        <panel className="facet-panel-tree">
            <Header
                dispatch={dispatch}
                label={label}
                facetsMetadata={facetsMetadata}
                appliedFacetFilters={selectedOptions}
                nowId={nowId}
            />
            <PanelList
                dispatch={dispatch}
                facets={facetsWithOptions}
                facetsMetadata={facetsMetadata}
                appliedFacetFilters={selectedOptions}
                truncateAppliedFacets={truncateAppliedFacets}
                nowId={nowId}
            />
        </panel>
    ) : (
        <div className="facet-panel-tree-empty" />
    );
};

export default view;
