import './sn-search-facets-desktop';
import './sn-search-facets-mobile';
import { SIZE } from './common/constants';

const view = ({ properties }) => {
    const { facets, label, size, selectedOptions } = properties;

    const facetVariant =
        size === SIZE.SMALL ? (
            <sn-search-facets-mobile
                facets={facets}
                label={label}
                selectedOptions={selectedOptions}
            />
        ) : (
            <sn-search-facets-desktop
                facets={facets}
                label={label}
                selectedOptions={selectedOptions}
            />
        );
    return facetVariant;
};

export default view;
